const Note = ({ note }) => {
  return (
    <div className="col l3 m4 s6">
      <div className="card-panel z-depth-2">
        <h5 className="black-text">{note.title}</h5>
        <p className="blue-grey-text lighten-1">{note.content}</p>
      </div>
    </div>
  );
};

export default Note;
